* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.pageCoverGrey {
  background: #e4e4e4;
}

.ForgotPassword {
  text-align: center;
  margin: 0px 0px 20px;
  font-size: 17px;
}

.form-container {
  display: flex;
  height: 98vh;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.form-container .form-wrap {
  background: #fff;
  width: 40%;
  padding: 15px 20px;
}

.form-container .form-wrap h2 {
  text-align: center;
  margin: 0px 0px 20px;
  font-size: 22px;
  color: black;
}

.form-container .form-wrap .form-box {
  margin: 0px 0px 15px;
  width: 40%;
  height: 20%;
}

.form-container .form-wrap .form-box input[type="password"] {
  padding: 5px 8px;
  border-radius: 3px;
  border: 1px solid #353535;
  font-size: 17px;
  width: 100%;
}

.form-container .form-wrap .form-submit {
  display: flex;
  justify-content: center;
}

.form-container .form-wrap .form-submit input[type="submit"] {
  padding: 4px 10px;
  border: none;
  border-radius: 2px;
  background: #353535;
  color: #fff;
  font-weight: 800;
  cursor: pointer;
  font-size: 16px;
}
